import {http} from "utils/http";

export function register(data) {
    return http.post('/auth/local/register', data);
}

export function login(data) {
    return http.post('/auth/local', data);
}

export function me() {
    return http.get('/users/me', { includeBearer: true });
}

export function saveUser(id, data) {
    return http.put(`/users/${id}`, data, { includeBearer: true });
}

export function sendRegisteredUserToIntegromat(data) {
    http.post('https://hook.integromat.com/ay21i9t4a9a91d9k6ushxwm7ag0sa4sk', data);
}