import { observable, action, computed, makeObservable } from "mobx";
import { validateName, validatePhone } from "utils/validators";
import {createSubscription} from "api/subscriptions";

export default class SubscrpiptionsMW {

    userStore;
    subscrModalState = null;
    SUBSCR_MODAL_STATES = {
        SUBSCRIPTIONS: 'subscription',
        PERSONAL: 'personal',
        NONE: 'none'
    }
    activeLink = null;
    order = '';

    name = null;
    phone = null;
    building = null;
    money = null;
    region = null;
    peoples = null;
    rooms = null;

    constructor(userStore) {
        makeObservable(this, {
            subscrModalState: observable,
            name: observable,
            phone: observable,
            building: observable,
            money: observable,
            region: observable,
            peoples: observable,
            rooms: observable,
            subscribe: action.bound,
            subscribePersonal: action.bound,
            openSubscrModal: action.bound,
            openPersonalSubscrModal: action.bound,
            hideActiveSubscrModal: action.bound,
            setName: action.bound,
            setPhone: action.bound,
            setBuilding: action.bound,
            setMoney: action.bound,
            setRegion: action.bound,
            setPeoples: action.bound,
            setRooms: action.bound,
            isNameValid: computed,
            isPhoneValid: computed,
            isSubscriptionComplete: computed
        });
        this.userStore = userStore;
    }

    subscribe() {
        createSubscription({
            name: this.name,
            phone: this.phone,
            order: this.order,
            city: this.userStore.user.city.name,
            type: 'Замовлення підписки'
        }).finally(() => {
            // eslint-disable-next-line no-undef
            runWfpWdgt(this.activeLink);
            this.subscrModalState = this.SUBSCR_MODAL_STATES.NONE;
        });
    }

    subscribePersonal() {
        createSubscription({
            name: this.name,
            phone: this.phone,
            order: this.order,
            building: this.building,
            money: this.money,
            region: this.region,
            peoples: this.peoples,
            rooms: this.rooms,
            city: this.userStore.user.city.name,
            type: 'Замовлення персонального підбору'
        }).finally(() => {
            // eslint-disable-next-line no-undef
            runWfpWdgt(this.activeLink);
            this.subscrModalState = this.SUBSCR_MODAL_STATES.NONE;
        });
    }



    openSubscrModal(link, order) {
        this.subscrModalState = this.SUBSCR_MODAL_STATES.SUBSCRIPTIONS;
        this.activeLink = link;
        this.order = order;
    }

    openPersonalSubscrModal(link, order) {
        this.subscrModalState = this.SUBSCR_MODAL_STATES.PERSONAL;
        this.activeLink = link;
        this.order = order;
    }

    hideActiveSubscrModal() {
        this.subscrModalState = this.SUBSCR_MODAL_STATES.NONE
        this.activeLink = null;
        this.order = '';
    }

    setName(e) {
        this.name = e.target.value
    }

    setPhone(e) {
        this.phone = e.target.value;
    }

    setBuilding(e) {
        this.building = e.value;
    }

    setMoney(e) {
        this.money = e.value;
    }

    setRegion(e) {
        this.region = e.target.value;
    }

    setPeoples(e) {
        this.peoples = e.value;
    }

    setRooms(e) {
        this.rooms = e.value;
    }

    get isNameValid() {
        return !this.name || validateName(this.name);
    }

    get isPhoneValid() {
        return !this.phone ||validatePhone(this.phone);
    }

    get isSubscriptionComplete() {
        return Boolean(this.isNameValid && this.isNameValid && this.phone && this.name);
    }
}