import { observable, action, makeObservable, computed } from "mobx";
import qs from 'qs';

export default class FiltersMW {

    searchMode = 'list';
    searchModeOptions = [
        {name: 'Список', icon: 'bx bx-grid-horizontal', value: 'list'},
        {name: 'Мапа', icon: 'bx bx-map-alt', value: 'map'}
    ];
    address = '';
    apartmentType = 'apartment';
    apartmentTypes = [
        {name: 'Кімната', value: 'room'},
        {name: 'Квартира', value: 'apartment'}
    ];
    maxPrice = 50000;
    prices = [0, this.maxPrice];
    onlyModerated = false;
    isAdditionalFiltersExpanded = false;
    square = [0, null]
    metroStations = [];
    roomCount = [];
    regions = [];
    withVideo = false;
    userStore;

    constructor(userStore) {
        makeObservable(this, {
            searchMode: observable,
            address: observable,
            apartmentType: observable,
            prices: observable,
            onlyModerated: observable,
            isAdditionalFiltersExpanded: observable,
            square: observable,
            metroStations: observable,
            roomCount: observable,
            regions: observable,
            withVideo: observable,
            setRoomCountWithApartmentType: action.bound,
            setRoomCount: action.bound,
            setSearchMode: action.bound,
            setAddress: action.bound,
            setApartmentType: action.bound,
            setPrice: action.bound,
            setOnlyModerated: action.bound,
            setWithVideo: action.bound,
            toggleAdditionalFilters: action.bound,
            setMinSquare: action.bound,
            setMaxSquare: action.bound,
            setMetroStations: action.bound,
            setRegions: action.bound,
            filtersQuery: computed,
            roomCountWithApartmentType: computed
        });
        this.userStore = userStore;
    }

    setRoomCountWithApartmentType(data){
        if (data.value.length >= 1 && data.value.includes('Кімната')) {
            if (this.apartmentType === 'room') {
                this.setApartmentType({value: 'apartment'});
                this.roomCount = data.value.filter(el => el !== 'Кімната');
            } else {
                this.setApartmentType({value: 'room'});
                this.roomCount = [];
            }
        } else {
            this.setApartmentType({value: 'apartment'});
            this.roomCount = data.value;
        }
    }

    setRoomCount(e) {
        if (e.checked) this.roomCount.push(e.value);
        else this.roomCount = this.roomCount.filter(el => el !== e.value);
    }

    get roomCountWithApartmentType() {
        return this.apartmentType === 'room' ? [ 'Кімната', ...this.roomCount ] : this.roomCount;
    }

    setSearchMode(e) {
        if (e.value) this.searchMode = e.value;

    }

    setAddress(e) {
        this.address = e.target.value;
    }

    setApartmentType(e) {
        if (e.value) this.apartmentType = e.value;
    }

    setPrice(e) {
        if (e.value) this.prices = e.value;
        if (e.min) {
            if (e.min > this.maxPrice) this.prices[0] = this.maxPrice;
            else if (e.min < 0) this.prices[0] = 0;
            else this.prices[0] = e.min;
        }
        if (e.max) {
            if (e.max > this.maxPrice) this.prices[1] = this.maxPrice;
            else if (e.max < 0) this.prices[1] = 0;
            else this.prices[1] = e.max;
        }
    }

    setOnlyModerated(e) {
        this.onlyModerated = e.value;
    }

    setWithVideo(e) {
        this.withVideo = e.value;
    }

    toggleAdditionalFilters() {
        this.isAdditionalFiltersExpanded = !this.isAdditionalFiltersExpanded;
    }

    setMinSquare(e) {
        this.square[0] = e.value;
    }

    setMaxSquare(e) {
        this.square[1] = e.value;
    }

    setMetroStations(e) {
        this.metroStations = e.value;
    }

    setRegions(e) {
        this.regions = e.value;
    }

    get filtersQuery() {
        const acc = [];
        acc.push({ city: { _id: this.userStore.user.preferences.city.id } });
        if (this.address) acc.push({ address_contains: this.address });
        if (this.prices[0] > 0) acc.push({ price_gte: this.prices[0] });
        if (this.prices[1] < this.maxPrice) acc.push({ price_lte: this.prices[1] });
        if (this.square[0] > 0) acc.push({ square_gte: this.square[0] });
        if (this.square[1]) acc.push({ square_lte: this.square[1] });
        if (this.onlyModerated) acc.push({moderated: this.onlyModerated});
        if (this.apartmentType === 'room') acc.push({room_eq: true}); else acc.push({room_eq: false});
        if (this.apartmentType === 'apartment' && this.roomCount.length) {
            if (this.roomCount.includes('4+') && this.roomCount.length > 1) {
                const filteredCount = this.roomCount.filter(el => el !== '4+');
                acc.push({ _or: [{rooms_amount_gte: 4}, {rooms_amount: filteredCount}]})
            }
            else {
                if (this.roomCount.includes('4+')) acc.push({rooms_amount_gte: 4});
                const filteredCount = this.roomCount.filter(el => el !== '4+');
                if (filteredCount.length) acc.push({rooms_amount: filteredCount})
            }
        }
        if (this.metroStations.length) acc.push({ metro: this.metroStations.map(station => station.id) });
        if (this.regions.length) acc.push({ region: this.regions.map(station => station.id) });
        if (this.withVideo) acc.push({youtube_link_contains: 'http' });

        return qs.stringify({
            _where: acc
        });

    }

}