import { http } from "utils/http";

export function getApartments(
    filters,
    limit = 36,
    offset = 0
) {
    return http.get(`/apartments?_limit=${limit}&_start=${offset}${filters && `&${filters}`}&_sort=createdAt:DESC`)
}

export function getApartment(id) {
    return http.get(`/apartments/${id}`);
}

export function getApartmentsCount(filters) {
    return http.get(`/apartments/count${filters ? `?${filters}` : ''}`);
}