import { observable, action, makeObservable, reaction } from "mobx";
import { getApartments, getApartmentsCount } from "api/apartments";
import { debounce } from "utils/functionDecorators";

export default class CatalogStore {

    pageData = null;
    state = 'ready'; //loading, error, ready
    apartments = [];
    apartmentsCount = 24;
    filtersMW
    limit = 24;
    offset = 0;

    constructor(filtersMW) {
        makeObservable(this, {
            pageData: observable,
            state: observable,
            apartments: observable,
            apartmentsCount: observable,
            limit: observable,
            offset: observable,
            setState: action.bound,
            setApartments: action.bound,
            setPageCount: action.bound,
            setPageData: action.bound
        });

        this.filtersMW = filtersMW;
        this.loadApartmentsFromAPI = this.loadApartmentsFromAPI.bind(this);
        reaction(
            () => this.filtersMW.filtersQuery,
            debounce(this.loadApartmentsFromAPI, 2000)
        )
        reaction(() => this.limit, this.loadApartmentsFromAPI);
        reaction(() => this.offset, this.loadApartmentsFromAPI);
    }

    setState(state) {
        switch (state) {
            case 'loading':
            case 'ready': {
                this.state = state;
                break;
            }
            default: {
                this.state = 'error';
                console.err(state);
            }
        }
    }

    setApartments(data) {
        this.apartments = data;
    }

    setPageCount(count) {
        this.apartmentsCount = count;
    }

    setPageData(e) {
        this.limit = e.rows;
        this.offset = e.first;
    }

    loadApartmentsFromAPI() {
        if (this.state !== 'loading') {
            this.setState('loading');
            getApartmentsCount(this.filtersMW.filtersQuery).then(this.setPageCount);
            getApartments(this.filtersMW.filtersQuery, this.limit, this.offset).then(data => {
                this.setApartments(data);
                this.setState('ready');
            }).catch(err => this.setState(err));
        }
    }

}