import { observable, action, makeObservable, reaction } from "mobx";
import { getCities, getMetros } from "api/data";
import { getRegions } from "api/regions";
import qs from "qs";

export default class UIStore {

    authModalState = null;
    AUTH_MODAL_STATES = {
        LOGIN: 'login',
        REGISTRATION: 'registration',
        NONE: 'none'
    }
    userStore;
    cities = [];
    metros = [];
    regions = [];
    isFiltersVisible = false;
    editUserDataModalVisible = false;

    constructor(userStore) {
        this.userStore = userStore;
        makeObservable(this, {
            authModalState: observable,
            cities: observable,
            metros: observable,
            regions: observable,
            isFiltersVisible: observable,
            editUserDataModalVisible: observable,
            setCities: action.bound,
            setRegions: action.bound,
            setMetros: action.bound,
            openLoginModal: action.bound,
            openRegistrationModal: action.bound,
            hideActiveAuthModal: action.bound,
            toggleFilters: action.bound,
            toggleUserDataModal: action.bound,

        });
        getCities().then(data => {
            this.setCities(data);
        });
        getMetros().then(data => {
            this.setMetros(data);
        });
        getRegions(qs.stringify({
            _where: [{ city: [this.userStore.user.preferences.city.id] }]
        })).then(data => {
            this.setRegions(data);
        })
        reaction(
            () => this.userStore.user.preferences.city.id,
            () => {
                getRegions(qs.stringify({
                    _where: [{ city: [this.userStore.user.preferences.city.id] }]
                })).then(data => {
                    this.setRegions(data);
                })
            }
        )
    }

    setCities(data) {
        this.cities = data;
    }

    setRegions(data) {
        this.regions = data;
    }

    setMetros(data) {
        this.metros = data;
    }

    openLoginModal() {
        this.authModalState = this.AUTH_MODAL_STATES.LOGIN;
    }

    openRegistrationModal() {
        this.authModalState = this.AUTH_MODAL_STATES.REGISTRATION;
    }

    hideActiveAuthModal() {
        this.authModalState = this.AUTH_MODAL_STATES.NONE
    }

    toggleFilters() {
        this.isFiltersVisible = !this.isFiltersVisible;
    }

    toggleUserDataModal() {
        this.editUserDataModalVisible = !this.editUserDataModalVisible;
    }

}