import React, { useState, useRef } from 'react';
import styled from "styled-components";
import { Dialog } from "primereact/dialog";
import { inject, observer } from "mobx-react";
import { SNIPPETS } from "consts/styles";
import { InputText } from "primereact/inputtext";
import { PrimaryButton } from "components/public/buttons";
import { saveUser } from "api/user";
import { Toast } from 'primereact/toast';
import { validateEmail, validatePassword, validatePhone } from "utils/validators";

const UserEditModal = ({ userStore, uiStore }) => {

    const [ name, setName ] = useState(userStore.user.name);
    const [ email, setEmail ] = useState(userStore.user.email);
    const [ password, setPassword ] = useState('');
    const [ repeatPassword, setRepeatPassword ] = useState('');
    const [ phone, setPhone ] = useState(userStore.user.phone);
    const toast = useRef(null);

    const saveUserClickHandler = () => {
        const data = {};
        if (name && name.trim() !== '') {
            if (name !== userStore.user.name) data.name = name;
        } else {
            toast.current.show({severity: 'error', summary: 'Помилка', detail: 'Вкажіть ім\'я'});
        }
        if (email && email.trim() !== '') {
            if (email !== userStore.user.email) {
                if (validateEmail(email)) data.email = email;
                else {
                    toast.current.show({severity: 'error', summary: 'Помилка', detail: 'Некорректний email'});
                }
            }
        } else {
            toast.current.show({severity: 'error', summary: 'Помилка', detail: 'Вкажіть email'});
        }

        if (phone && phone.trim() !== '') {
            if (phone !== userStore.user.phone ) {
                if (validatePhone(phone)) data.phone = phone;
                else {
                    toast.current.show({severity: 'error', summary: 'Помилка', detail: 'Некорректний номер телефону'});
                }
            }
        } else {
            toast.current.show({severity: 'error', summary: 'Помилка', detail: 'Вкажіть номер телефону'});
        }

        if (password && password.trim() !== '') {
            if (validatePassword(password)) {
                if (password === repeatPassword) data.password = password;
                else {
                    toast.current.show({severity: 'error', summary: 'Помилка', detail: 'Паролі не збігаються'});
                }
            }
            else {
                toast.current.show({severity: 'error', summary: 'Помилка', detail: 'Вкажіть валідний пароль'});
            }
        }

        saveUser(userStore.user.id, data)
            .then(data => userStore.setUserData(data))
            .catch(e => console.error(e))
    }

    return (
        <>
            <Modal
                onHide={uiStore.toggleUserDataModal}
                visible={uiStore.editUserDataModalVisible}
                header={<Header>Особиста інформація</Header>}
            >
                <div className="first-container">
                    <div className="p-inputgroup">
                        <InputText
                            type="email"
                            value={email}
                            placeholder="Email"
                            onChange={e => setEmail(e.target.value)}
                        />
                    </div>
                    <div className="p-inputgroup">
                        <InputText
                            type="text"
                            value={name}
                            placeholder="Ім'я"
                            onChange={e => setName(e.target.value)}
                        />
                    </div>
                    {/*<label htmlFor="avatarInput">*/}
                    {/*    <Avatar shape='circle' label={<i className='bx bx-camera'/>} />*/}
                    {/*    <input type="file" id="avatarInput" hidden />*/}
                    {/*</label>*/}
                </div>
                <hr/>
                <div className="p-inputgroup">
                    <InputText
                        type="password"
                        value={password}
                        placeholder="Новий пароль"
                        onChange={e => setPassword(e.target.value)}
                    />
                </div>
                <div className="p-inputgroup">
                    <InputText
                        type="password"
                        value={repeatPassword}
                        placeholder="Повторіть пароль"
                        onChange={e => setRepeatPassword(e.target.value)}
                    />
                </div>
                <hr/>
                <div className="p-inputgroup">
                    <InputText
                        type="text"
                        value={phone}
                        placeholder="Телефон"
                        onChange={e => setPhone(e.target.value)}
                    />
                </div>
                <hr/>
                <PrimaryButton onClick={saveUserClickHandler}>Зберегти</PrimaryButton>
            </Modal>
            <Toast ref={toast} />
        </>
    )
};

export default inject('userStore', 'uiStore')(observer(UserEditModal));

const Modal = styled(Dialog)`
  color: ${props => props.theme.colors.text.important};
  .p-dialog-header-close {
    margin-left: 20px;
    ${SNIPPETS.noOutline};
  }
  
  .p-inputgroup {
    margin: 10px auto;
    input {
      width: 300px;
      ${SNIPPETS.noOutline};
    }
  }
  
  .first-container {
    display: grid;
    grid-gap: 0 20px;
    //grid-template-columns: 300px 120px;
    label {
      //grid-column: 2/3;
      //grid-row: 1/3;
      justify-self: center;
      align-content: center;
    }
    .p-avatar {
      border-radius: 50%;
      height: 110px;
      width: 110px;
      font-size: 3rem;
      cursor: pointer;
      &:hover {
        filter: brightness(.9);
      }
    }
  }
  
  button {
    width: 100% !important;
    margin: 10px 0;
  }

  hr {
    height: 1px;
    width: 100%;
    border: none;
    background: rgba(115, 115, 115, .5);
    margin-bottom: 35px;
  }
`;


const Header = styled.h3`
  font-size: 2rem;
  line-height: 1.1;
  font-weight: 700;
  margin: 30px 0 0 0;
  display: flex;
  color: ${props => props.theme.colors.text.important};
`;
