import React, { useRef } from "react";
import styled from 'styled-components';
import { Dropdown } from "primereact/dropdown";
import logo from 'images/logo.png'
import { inject, observer } from "mobx-react";
import { routes } from "consts/routes";
import { Link } from "react-router-dom";
import { SNIPPETS } from "consts/styles";
import { PrimaryButton } from "components/public/buttons";
import {Login, Registration} from "components/Auth";
import UserPane from "components/Сabinet/UserPane";

const Header = ({ uiStore, userStore }) => {
    const menuRef = useRef(null);
    const buttonRef = useRef(null);

    function setMenuState(state) {
        if (state === 'opened') menuRef.current.classList.add('opened');
        else menuRef.current.classList.remove('opened');
    }

    function toggleMenuButtonClickHandler(e) {
        if (e.target.tagName === 'SPAN') {
            if (e.target.parentNode.classList.contains('active')) {
                e.target.parentNode.classList.remove('active');
                setMenuState('closed');
            }
            else {
                e.target.parentNode.classList.add('active');
                setMenuState('opened');
            }
        } else if (e.target.classList.contains('active')) {
            e.target.classList.remove('active');
            setMenuState('closed');
        }
        else {
            e.target.classList.add('active');
            setMenuState('opened');
        }
    }

    const closeMenu = () => {
        setMenuState('closed');
        buttonRef.current.classList.remove('active');
    }

    return (
        <>
            <Container>
                <Menu ref={menuRef}>
                    <Link to={routes.BASE} >
                        <LogoImg src={logo} alt="Логотип сайту"/>
                    </Link>
                    <div className="links">
                        <LinkItem to={routes.APARTMENTS} onClick={closeMenu} >Знайти квартиру 🏠</LinkItem>
                        <LinkItem to={routes.PERSONAL}  onClick={closeMenu} >Персональний підбір 👨</LinkItem>
                        <LinkItem to={routes.TARRIFS} onClick={closeMenu} >Тарифи 💰</LinkItem>
                    </div>
                    {uiStore.cities.length && <Dropdown
                        className="city"
                        optionLabel="name"
                        optionValue="id"
                        options={uiStore.cities}
                        value={ userStore.user.city.id || uiStore.cities[0].id }
                        onChange={ e => {
                            userStore.user.setCity(uiStore.cities.find(city => city.id === e.value));
                        }}
                    />}
                    <div className="user">
                        {false && <PrimaryButton>Здати квартиру</PrimaryButton>}
                        <UserPane />
                    </div>
                    <AnimatedButton ref={buttonRef} onClick={toggleMenuButtonClickHandler}><span/></AnimatedButton>
                </Menu>
            </Container>
            <Decoy/>
            <Login />
            <Registration />
        </>
    )
}

export default inject('userStore', 'uiStore' )(observer(Header));

const Container = styled.div`
  width: 100%;
  max-width: 100vw;
  position: fixed;
  z-index: 3;
  display: flex;
  justify-content: center;
  background: white;
`

const Menu = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
  max-width: ${props => props.theme.consts.containerWidth};
  margin: 20px auto;
  transition: height .5s;
  min-height: 54px;
  padding: 0 20px;
  
  @media(max-width: 1100px) {
    flex-direction: column;
    .user,
    .links,
    .city {
      height: 0;
    }
    &:not(.opened) .city {
      overflow: hidden;
      border: none;
    }
    &.opened .user {
      height: 110px;
    }
    &.opened .links {
      height: 116px;
      margin-bottom: 10px;
    }
    &.opened .city {
      height: 40px;
      margin-bottom: 10px;
    }
  }
  
  .city {
    width: max-content;
    align-self: center;
  }
  
  .p-dropdown-panel {
    position: absolute !important;
  }
  
  .user,
  .links {
    transition: height .5s;
    display: flex;
    flex-direction: inherit;
    align-items: center;
    overflow: hidden;
    gap: 10px;
  }
  
  button {
    width: max-content; 
  }
  a {
    width: max-content;
  }
`;

const LogoImg = styled.img`
  height: 30px;
  margin-top: 12px;
  margin-left: 20px;
`;

const LinkItem = styled(Link)`
  text-decoration: none;
  margin-left: 5px;
  font-size: 16px;
  line-height: 16px;
  padding: 8px;
  ${SNIPPETS.linkColor}
`;

const AnimatedButton = styled.div`
  
  @media(max-width: 1100px) {
    visibility: visible;
  }
  visibility: hidden;
  position: absolute;
  right: 20px;
  top: 30px;
  cursor: pointer;
  height: 30px;
  width: 30px;

  span {
    transition: transform .3s ease-in-out, top .2s ease-in-out .2s, background 0ms .2s;
    height: 3px;
    width: 25px;
    background: #414141;
    position: absolute;
    top: 15px;

    &:before,
    &:after {
      transition: transform .3s ease-in-out, top .2s ease-in-out .2s;
      content: '';
      height: 3px;
      width: 25px;
      background: #414141;
      position: absolute;
    }

    &:before {
      top: -8px;
    }

    &:after {
      top: 8px;
    }
  }

  &.active span {
    background: transparent;
    transition: transform .3s ease-in-out .2s, top .2s, background 0ms .2s;

    &:before,
    &:after {
      top: 0;
      transition: transform .3s ease-in-out .2s, top .2s;
    }

    &:before {
      transform: rotate(135deg);
    }

    &:after {
      transform: rotate(225deg);
    }
  }
`;

const Decoy = styled.div`
  display: block;
  height: 94px;
  width: 100%;
`
