import React, { Component } from 'react';
import 'index.css';
import { ThemeProvider } from "styled-components";
import { lightTheme } from "consts/styles";
import {HashRouter} from "react-router-dom";
import { Provider } from "mobx-react";
import { UserStore } from "stores/UserStore";
import UIStore from "stores/UIStore";
import 'primereact/resources/primereact.min.css';
import 'theme.css';
import 'primeicons/primeicons.css';
import CatalogStore from "stores/CatalogStore";
import FiltersMW from "models/FiltersMW";
import {AuthMW} from "models/AuthMW";
import TokenStore from "stores/TokenStore";
import Routes from "app/Routes";
import SubscrpiptionsMW from "models/SubscrpiptionsMW";

const userStore = new UserStore();
const uiStore = new UIStore(userStore);
const filtersMW = new FiltersMW(userStore);
const catalogStore = new CatalogStore(filtersMW);
const tokenStore = new TokenStore();
const authMW = new AuthMW(userStore, tokenStore);
const subscrMW = new SubscrpiptionsMW(userStore);

const stores = {
  userStore,
  uiStore,
  catalogStore,
  filtersMW,
  authMW,
  subscrMW
}

class App extends Component {

  render() {
    return (
      <ThemeProvider theme={lightTheme}>
        <Provider {...stores}>
          <HashRouter>
            <Routes />
          </HashRouter>
        </Provider>
      </ThemeProvider>
    );

  }

}

export default App;

export {
  tokenStore
}
