import { observable, action, computed, makeObservable } from "mobx";
import { validateEmail, validatePassword, validatePhone } from "utils/validators";
import {login, register, sendRegisteredUserToIntegromat} from "api/user";
import User from "stores/models/User";

export class AuthMW {

    email = '';
    password = '';
    rememberMe = false;
    phone = '';
    name = '';
    userStore = null;
    tokenStorage = null;
    loginError = null;
    registerError = null;

    constructor(userStore, tokenStorage) {
        makeObservable(this, {
            email: observable,
            password: observable,
            rememberMe: observable,
            phone: observable,
            name: observable,
            loginError: observable,
            registerError: observable,
            setLoginError: action.bound,
            setRegisterError: action.bound,
            setEmail: action.bound,
            setPassword: action.bound,
            setRememberMe: action.bound,
            setPhone: action.bound,
            setName: action.bound,
            clearAll: action.bound,
            logout: action.bound,
            canRegister: computed,
            isEmailValid: computed,
            isPasswordValid: computed,
            isPhoneValid: computed,
            serverContract: computed,
            loginContract: computed,
        });
        this.userStore = userStore;
        this.tokenStorage = tokenStorage;
        this.registerUser = this.registerUser.bind(this);
        this.login = this.login.bind(this);
    }

    setLoginError(str) {
        this.loginError = str;
    }

    setRegisterError(str) {
        this.registerError = str;
    }

    setEmail(e) {
        this.email = e.target.value;
    }

    get isEmailValid() {
        return !this.email || validateEmail(this.email)
    }

    setPassword(e) {
        this.password = e.target.value;
    }

    get isPasswordValid() {
        return !this.password || validatePassword(this.password)
    }

    setRememberMe(e) {
        this.rememberMe = e.checked;
    }

    setPhone(e) {
        this.phone = e.target.value;
    }

    get isPhoneValid() {
        return !this.phone || validatePhone(this.phone);
    }

    setName(e) {
        this.name = e.target.value;
    }


    clearAll() {
        this.email = '';
        this.password = '';
        this.rememberMe = false;
        this.phone = '';
    }

    get serverContract() {
        return ({
            username: this.email,
            email: this.email,
            password: this.password,
            phone: this.phone,
            name: this.name
        });
    }

    get loginContract() {
        return ({
            identifier: this.email,
            password: this.password
        });
    }

    get canRegister() {
        return this.isEmailValid &&
            this.isPasswordValid &&
            this.isPhoneValid &&
            this.email &&
            this.password &&
            this.phone &&
            this.name
    }

    registerUser() {
        return register(this.serverContract).then(data => {
            this.tokenStorage.activeStorage.setItem('token', data.jwt);
            this.userStore.setUserData(data.user);
            this.userStore.setIsLogged(true);
            sendRegisteredUserToIntegromat({
                email: this.email,
                phone: this.phone,
                name: this.name
            });
        }).catch(resp => {
            if (resp.data.message[0].messages[0].message)
                this.setRegisterError(resp.data.message[0].messages[0].message)
            return Promise.reject()
        });
    }

    login() {
        if (this.rememberMe) {
            this.tokenStorage.setStorage('local');
        } else {
            this.tokenStorage.setStorage('session');
        }
        return login(this.loginContract).then(data => {
            this.tokenStorage.activeStorage.setItem('token', data.jwt);
            this.userStore.setUserData(data.user);
            this.userStore.setIsLogged(true);
        }).catch(resp => {
            if (resp.data.message[0].messages[0].message) {
                this.setLoginError(resp.data.message[0].messages[0].message);
            }
            return Promise.reject()
        })
    }

    logout() {
        this.userStore.setIsLogged(false);
        this.userStore.setUserData(new User());
        this.tokenStorage.activeStorage.removeItem('token');
    }
}