import React from 'react';
import styled from "styled-components";
import { Dialog } from 'primereact/dialog';
import { InputText } from "primereact/inputtext";
import { inject, observer } from "mobx-react";
import logo from 'images/logo.png'
import { SNIPPETS } from "consts/styles";
import { Checkbox } from "primereact/checkbox";
import { PrimaryButton } from "components/public/buttons";

const Login = inject('uiStore', 'authMW' )(observer(({ uiStore, authMW }) => {

    const onLoginClick = () => {
        authMW.login().then(() => {
            uiStore.hideActiveAuthModal();
        })
    }

    return (
        <Modal
            onHide={() => {
                uiStore.hideActiveAuthModal()
                authMW.clearAll();
            }}
            visible={uiStore.authModalState === uiStore.AUTH_MODAL_STATES.LOGIN}
            header={<Header>Вітаємо тебе у <img src={logo} alt="Лого"/>!</Header>}
        >
            <div className="p-inputgroup">
                <span className="p-inputgroup-addon">
                    <i>@</i>
                </span>
                <InputText
                    className={`p-inputtext-lg ${!authMW.isEmailValid && 'p-invalid '}`}
                    type="email"
                    onInput={authMW.setEmail}
                    placeholder="Email"
                />
            </div>
            <div className="p-inputgroup">
                <span className="p-inputgroup-addon">
                    <i className="bx bxs-lock" />
                </span>
                <InputText
                    className={`p-inputtext-lg ${!authMW.isPasswordValid && 'p-invalid '}`}
                    type="password"
                    placeholder="Пароль"
                    onInput={authMW.setPassword}
                />
            </div>
            <div>
                <Checkbox id="rememberMe" checked={authMW.rememberMe} onChange={authMW.setRememberMe}/>
                <label htmlFor="rememberMe"> Запам'ятати мене</label>
            </div>
            <PrimaryButton onClick={onLoginClick}>Увійти</PrimaryButton>
            {authMW.loginError && <p style={{color: 'red'}}>{authMW.loginError}</p>}
            <div className="footer">
                <span>Новенький?</span>
                <LinkButton onClick={uiStore.openRegistrationModal}>Створи новий аккаунт 😎</LinkButton>
            </div>
        </Modal>
    )
}));


const Registration = inject('uiStore', 'authMW' )(observer(({ uiStore, authMW}) => {

    const onRegisterClick = () => {
        authMW.registerUser().then(() => {
            authMW.clearAll();
            uiStore.hideActiveAuthModal();
        })
    }

    return (
        <Modal
            onHide={() => {
                uiStore.hideActiveAuthModal()
                authMW.clearAll();
            }}
            visible={uiStore.authModalState === uiStore.AUTH_MODAL_STATES.REGISTRATION}
            header={<Header>Йоу, тут у нас новенький 😏</Header>}
        >
            <div className="p-inputgroup">
                <span className="p-inputgroup-addon">
                    <i>@</i>
                </span>
                <InputText
                    className={!authMW.isEmailValid && 'p-invalid '}
                    type="email"
                    onInput={authMW.setEmail}
                    placeholder="Email"
                />
            </div>
            <div className="p-inputgroup">
                <span className="p-inputgroup-addon">
                    <i className="bx bxs-lock" />
                </span>
                <InputText
                    className={!authMW.isPasswordValid && 'p-invalid '}
                    type="password"
                    onInput={authMW.setPassword}
                    placeholder="Вводь пароль, тільки давай без 123456"
                />
            </div>
            <div className="p-inputgroup">
                <span className="p-inputgroup-addon">
                    <i className="bx bx-phone" />
                </span>
                <InputText
                    className={!authMW.isPhoneValid && 'p-invalid '}
                    type="phone"
                    onInput={authMW.setPhone}
                    placeholder="Номер телефону"
                />
            </div>
            <div className="names">
                <InputText placeholder="Звати як?" onInput={authMW.setName}/>
            </div>
            <PrimaryButton disabled={!authMW.canRegister} onClick={onRegisterClick}>Зареєструватися</PrimaryButton>
            {authMW.registerError && <p style={{color: 'red'}}>{authMW.registerError}</p>}
            <div className="footer">
                <span>Вже є аккаунт?</span>
                <LinkButton onClick={uiStore.openLoginModal}>Увійти 🚪</LinkButton>
            </div>
        </Modal>
    )
}));

export {
    Login,
    Registration
};

const Modal = styled(Dialog)`
  color: ${props => props.theme.colors.text.important};
  .p-dialog-header-close {
    margin-left: 20px;
    ${SNIPPETS.noOutline};
  }
  
  .p-inputgroup {
    margin: 10px auto;
    input {
      ${SNIPPETS.noOutline};
    }
  }
  button {
    width: 100%;
    margin: 10px 0;
  }
  
  .footer {
    display: flex;  
  }
  .names {
    display: grid;
    input {${SNIPPETS.noOutline};}
  }
`;

const Header = styled.h3`
  font-size: 2rem;
  line-height: 1.1;
  font-weight: 700;
  margin: 30px 0 0 0;
  display: flex;
  color: ${props => props.theme.colors.text.important};
  img {
    height: 2rem;
    margin: auto;
    padding-left: 10px;
  }
`;

const LinkButton = styled.button`
  padding: 0;
  border: none;
  background: none;
  color: ${props => props.theme.colors.accent.main};
  font-weight: 400;
  line-height: 1.5;
  margin: 0 0 0 10px !important;
  font-size: 1rem;
  cursor: pointer;
  width: max-content !important;
  &:hover {
    text-decoration: underline;
  }
`;