import { makeObservable, action, observable, computed} from "mobx";
import { saveUser } from "api/user";

export default class User {

    preferences = {
        city: {
            id: "5f24432389c1fa00853614a9",
            name: "Київ ❤️",
            name_site: "в Києві ❤️",
            _id: "5f24432389c1fa00853614a9"
        }
    }
    name = null;
    email = null;
    phone = null;
    id = null;
    liked_apartments = null;
    visited_apartments = null;
    subscription = null;
    days_of_subscription = 0;

    constructor(serverUser = {}) {
        makeObservable(this, {
            preferences: observable.deep,
            name: observable,
            email: observable,
            phone: observable,
            liked_apartments: observable,
            visited_apartments: observable,
            subscription: observable,
            days_of_subscription: observable,
            setCity: action.bound,
            setLikedApartments: action.bound,
            setVisitedApartments: action.bound,
            city: computed,
            cityName: computed,
            hasSubscription: computed
        });
        this.addLikedApartment = this.addLikedApartment.bind(this);
        this.addVisitedApartment = this.addVisitedApartment.bind(this);

        if (serverUser.preferences) this.preferences = serverUser.preferences;
        this.name = serverUser.name;
        this.email = serverUser.email;
        this.phone = serverUser.phone;
        this.subscription = typeof serverUser.subscription === 'object' ? serverUser.subscription._id : serverUser.subscription;
        this.days_of_subscription = serverUser.days_of_subscription;
        this.id = serverUser.id;
        if (serverUser.liked_apartments) {
            if (typeof serverUser.liked_apartments[0] === 'string') {
                this.liked_apartments = serverUser.liked_apartments;
            } else this.liked_apartments = serverUser.liked_apartments.map(el => el._id);
        } else  {
            this.liked_apartments = [];
        }
        if (serverUser.visited_apartments) {
            if (typeof serverUser.visited_apartments[0] === 'string') {
                this.visited_apartments = serverUser.visited_apartments;
            } else this.visited_apartments = serverUser.visited_apartments.map(el => el._id);
        } else  {
            this.visited_apartments = [];
        }
    }

    setCity(city) {
        this.preferences.city = city;
        saveUser(this.id, {preferences: this.preferences})
            .catch(e => console.error(e));
    }

    get city() {
        return this.preferences.city
    }

    get cityName() {
        return this.preferences.city.name
    }

    get hasSubscription() {
        return this.days_of_subscription > 0;
    }

    setLikedApartments(liked_apartments) {
        this.liked_apartments = liked_apartments.map(el => el._id);
    }

    setVisitedApartments(visited_apartments) {
        this.visited_apartments = visited_apartments.map(el => el._id);
    }

    addLikedApartment(id) {
        if (this.liked_apartments.includes(id)) {
            this.liked_apartments = this.liked_apartments.filter(el => el !== id);
        } else this.liked_apartments.push(id);
        saveUser(this.id, {liked_apartments: this.liked_apartments})
            .then(data => this.setLikedApartments(data.liked_apartments))
            .catch(e => console.error(e));
    }

    addVisitedApartment(id) {
        if (!this.visited_apartments.includes(id))
            this.visited_apartments.push(id);
        saveUser(this.id, {visited_apartments: this.visited_apartments})
            .then(data => this.setVisitedApartments(data.visited_apartments))
            .catch(e => console.error(e));
    }
}
