export default class TokenStore {

    activeStorage;

    constructor() {
        this.activeStorage = localStorage;
        this.setStorage = this.setStorage.bind(this);
    }

    setStorage(type) {
        switch (type) {
            case 'session': {
                this.activeStorage = sessionStorage;
                break;
            }
            case 'local':
            default: {
                this.activeStorage = localStorage;
            }
        }
    }

}