import React, { useEffect, Suspense } from 'react';
import { Route } from "react-router-dom";
import { routes } from "consts/routes";
import { withRouter } from "react-router";
import Preloader from "../components/public/Preloader";
import Header from "components/Header";
import Footer from "components/Footer";
const MainPage = React.lazy(() => import("components/MainPage"));
const Catalog = React.lazy(() => import("components/Catalog"));
const ApartmentView = React.lazy(() => import("components/ApartmentView"));
const Tariffs = React.lazy(() => import("components/Tariffs"));
const Personal = React.lazy(() => import("components/Personal"));
const Questions = React.lazy(() => import("components/Personal/Questions"));
const Cabinet = React.lazy(() => import("components/Сabinet/index"));

const Routes = withRouter(({ location }) => {

    function onRouteChanged() {
        window.scrollTo(0, 0)
    }

    useEffect(() => {
        onRouteChanged();
    }, [ location ])

    return (
        <>
            <Header />
            <Route exact path={routes.BASE} render={() => <Suspense fallback={<Preloader />}><MainPage /></Suspense>}/>
            <Route exact path={routes.APARTMENTS} render={() => <Suspense fallback={<Preloader />}><Catalog /></Suspense>}/>
            <Route path={routes.TARRIFS} render={() => <Suspense fallback={<Preloader />}><Tariffs /></Suspense>} />
            <Route path={`${routes.APARTMENT}:id`} render={() => <Suspense fallback={<Preloader />}><ApartmentView /></Suspense>} />
            <Route path={routes.PERSONAL} render={() => <Suspense fallback={<Preloader />}><Personal /></Suspense>} />
            <Route path={routes.QUESTIONS} render={() => <Suspense fallback={<Preloader />}><Questions /></Suspense>} />
            <Route path={routes.CABINET} render={() => <Suspense fallback={<Preloader />}><Cabinet /></Suspense>} />
            <Footer />
        </>
    );
})

export default Routes;

