function validateEmail(email) {
    // eslint-disable-next-line no-useless-escape
    const re = /^(([^<>()[\]\.,;:\s@\"]+(\.[^<>()[\]\.,;:\s@\"]+)*)|(\".+\"))@(([^<>()[\]\.,;:\s@\"]+\.)+[^<>()[\]\.,;:\s@\"]{2,})$/i;
    return re.test(String(email).toLowerCase());
}

function validatePhone(phone) {
    const re = /^\+?[0-9]{10,12}$/;
    return re.test(String(phone).toLowerCase());
}

function validatePassword(password) {
    const lowerCaseRegex = '(?=.*[a-zа-я])';
    const upperCaseRegex = '(?=.*[A-ZА-Я])';
    const numericRegex = '(?=.*[0-9])';
    const re = new RegExp(
        `^((${lowerCaseRegex}${upperCaseRegex})|(${lowerCaseRegex}${numericRegex})|(${upperCaseRegex}${numericRegex})|(${upperCaseRegex})|(${lowerCaseRegex})|(${numericRegex}))(?=.{6,})`
    );

    return re.test(String(password).toLowerCase());
}

function validateName(str) {
    // eslint-disable-next-line no-useless-escape
    const re = /^[0-9a-zA-ZА-Яа-яіґєїёІҐЄЇЁ\s\`\'\-]{3,}$/;
    return re.test(str.trim());
}

export {
    validateEmail,
    validatePassword,
    validatePhone,
    validateName
}