import React, { useRef } from 'react';
import styled from "styled-components";
import { Avatar } from 'primereact/avatar';
import { Button } from "primereact/button";
import { SNIPPETS } from "consts/styles";
import { inject, observer } from "mobx-react";
import { OverlayPanel } from "primereact/overlaypanel";
import { Link } from "react-router-dom";
import { routes, cabinetStates } from "consts/routes";
import UserEditModal from "components/Сabinet/UserEditModal";


const UserPane = ({ userStore, authMW, uiStore }) => {

    const op = useRef(null);

    if (userStore.isLogged) return (<>
        <HoveredUserData onClick={(e) => op.current.toggle(e)} >
            <Avatar label={userStore.user.name[0]}  shape="circle" />
            <p>{userStore.user.name}</p>
        </HoveredUserData>
        <CabinetPanel ref={op}>
            {/*<Link to={routes.CABINET + cabinetStates.myApartments } onClick={() => op.current.hide()}><i className='bx bx-home'/> <span>Мої оголошення</span></Link>*/}
            <Link to={routes.CABINET + cabinetStates.visited } onClick={() => op.current.hide()}><i className='bx bx-time'/> <span>Історія переглядів</span></Link>
            <Link to={routes.CABINET + cabinetStates.liked } onClick={() => op.current.hide()}><i className='bx bx-heart'/> <span>Обране</span></Link>
            <Link to={routes.CABINET + cabinetStates.subscriptions } onClick={() => op.current.hide()}><i className='bx bx-chevron-up-circle'/> <span>Мої підписки</span></Link>
            <hr/>
            <UserData>
                <Avatar label={userStore.user.name[0]}  shape="circle" />
                <div>
                    <span>Ви увійшли як:</span>
                    <span>{userStore.user.email}</span>
                </div>
            </UserData>
            <LinkButton onClick={uiStore.toggleUserDataModal}>Дані профіля</LinkButton>
            <LinkButton onClick={authMW.logout}>Вийти</LinkButton>
        </CabinetPanel>
        <UserEditModal />
    </>)
    return (<LinkButton className="p-button-link" onClick={uiStore.openLoginModal} >Увійти</LinkButton>)
}

export default inject('userStore', 'authMW', 'uiStore')(observer(UserPane));

const LinkButton = styled(Button)`
    ${SNIPPETS.linkColor};
    ${SNIPPETS.noOutline};
    background: none;
    border: none;
    outline: none !important;
    color: ${props => props.theme.colors.link.main} !important;
    &:hover {
      color: ${props => props.theme.colors.link.hover} !important;
      background: none !important;
      border: none !important;
    }
    &:active {
      background: none !important;
      border: none !important;
    }
`;

const UserData = styled.div`
  display: flex;
  flex-direction: row;
  gap: 10px;
  align-items: center;
  .p-avatar {
    border-radius: 50% !important;
  }
  
  span {
    font-size: .7rem;
    display: block;
  }
`;

const HoveredUserData = styled(UserData)`
  cursor: pointer;
  &:hover {
    .p-avatar {
      filter: brightness(.9);
    }
    p {
      text-decoration: underline;
    }
  }
`;



const CabinetPanel = styled(OverlayPanel)`
  max-width: 250px;
  width: 90%;
  @media(max-width: 1100px) {
    top: 320px !important;
    left: auto !important;
    right: auto !important;
  }
  top: 70px !important;
  .p-overlaypanel-content {
    display: flex;
    flex-direction: column;
    padding: 8px 16px;

    a {
      ${SNIPPETS.linkColor};
      text-decoration: none;
      padding: 8px 0;
      display: flex;
      align-items: center;
      i {
        font-size: 1.5rem;
      }
    }

    hr {
      height: 1px;
      width: 100%;
      border: none;
      background: #737373;
      opacity: .5;
    }

    button {
      padding: 8px 0;
    }
  }
`;