import {observable, action, computed, makeObservable} from "mobx";
import User from "stores/models/User";
import {me, saveUser} from "api/user";

export class UserStore {

    userData = new User();
    isLogged = false;
    isLoading = true;

    constructor() {
        makeObservable(this, {
            userData: observable,
            isLogged: observable,
            isLoading: observable,
            setUserData: action.bound,
            setIsLoading: action.bound,
            setIsLogged: action.bound,
            user: computed,
        });
        me().then(data => {
            this.setUserData(data);
            this.setIsLogged(true);
        }).catch(e => {
            console.error(e);
            this.setIsLogged(false);
        }).finally(() => {
            this.setIsLoading(false);
        })

        this.saveCurrentUser = this.saveCurrentUser.bind(this);
    }

    setUserData(userData) {
        this.userData = new User(userData);
    }

    setIsLoading(value) {
        this.isLoading = value;
    }

    setIsLogged(value) {
        this.isLogged = value;
    }

    get user() {
        return this.userData;
    }

    saveCurrentUser() {
        saveUser(this.user.id, this.user)
            .then(data => this.setUserData(data))
            .catch(e => console.error(e))
    }

}